body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #ef6a4e;
}

.container {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.form {
  background: #fff;
  padding: 50px;
  border-radius: 15px;
  box-shadow: 1px 1px 10px #772b1b;
}

.heading {
  font-weight: 700;
  margin-top: 20px;
}

.text {
  color: #6d6969;
}

.form-control {
  border: none;
  border-radius: 0;
  background: #f1f1f1;
  margin-top: 20px;
}

.form-control:focus {
  background: #f1f1f1;
}

.btn-danger {
  background: #ee5a44;
  border-color: #ee5a44;
  padding: 5px 30px;
  border-radius: 25px;
}

